import React from 'react';
import s from './FormButton.module.scss';
import cn from 'classnames/bind';

const cx = cn.bind(s);

const FormButton = ({
  children,
  className,
  hasError,
  validating,
  ...props
}) => (
  <button
    type="submit"
    className={cx('button', className, {
      validating,
      error: hasError,
      valid: !hasError,
    })}
    {...props}
  >
    {children}
  </button>
);

export default FormButton;
