import React, { useState } from 'react';
import Form from '../Form/Form';
import FormInput from '../FormInput/FormInput';
import StatusBlock from '../StatusBlock/StatusBlock';
import FormButton from '../FormButton/FormButton';
import s from './App.module.scss';

const App = () => {
  const DEBOUNCE_NUMBER = 500;
  const [values, setValues] = useState({
    greetingText: '',
  });
  const [showForm, setShowForm] = useState(true);
  const [validating, setValidating] = useState(false);
  const [hasError, setHasError] = useState(false);
  let timeout;

  const validate = (text) => {
    switch (text) {
      case 'Hello':
        setHasError(false);
        break;
      case 'Hi':
        setHasError(false);
        break;
      default:
        setHasError(true);
        break;
    }
  };

  const submitHandler = (evt) => {
    evt.preventDefault();
    setShowForm(false);
  };

  const debounce = (value) => {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => {
      validate(value);

      setValidating(false);
    }, DEBOUNCE_NUMBER);
  };

  const changeHandler = (name, evt) => {
    setValidating(true);
    setValues({
      ...values,
      [name]: evt.target.value,
    });
    debounce(evt.target.value);
  };

  const backHandler = () => {
    setShowForm(true);
  };

  return (
    <div className={s.wrapper}>
      {showForm && (
        <Form onSubmit={submitHandler}>
          <FormInput
            name="greetingText"
            label="Greeting text"
            placeholder="Please type your text"
            onChange={(evt) => changeHandler('greetingText', evt)}
          />
          {values.greetingText && (
            <StatusBlock {...{ values, hasError, validating }} />
          )}
          <FormButton
            disabled={hasError || validating}
            {...{ hasError, validating }}
          >
            Send
          </FormButton>
        </Form>
      )}
      {!showForm && (
        <button className={s.backButton} onClick={backHandler}>
          Back
        </button>
      )}
    </div>
  );
};

export default App;
