import React from 'react';
import PropTypes from 'prop-types';
import s from './StatusBlock.module.scss';
import cn from 'classnames/bind';

const cx = cn.bind(s);

const StatusBlock = ({ values, hasError, validating }) => {
  const preText =
    (validating && 'Checking availability...') ||
    (hasError && 'Please check your text') ||
    'is available';
  return (
    <div
      className={cx('wrapper', {
        validating,
        error: hasError,
        valid: !hasError,
      })}
    >
      <span className={s.greeting}>
        <b>{values.greetingText}</b>{' '}
      </span>
      <span className={s.text}>{preText}</span>
    </div>
  );
};

export default StatusBlock;
